body {
    background: url("/images/bg.jpg") center center/cover no-repeat fixed;
    font-family: 'Montserrat', sans-serif !important;
}

.card {
    position: relative;
    overflow: hidden;
}

.active-link::after {
    content: '';
    display: block;
    margin: 0 auto;
    width: 50%;
    border-bottom: 2px solid teal;
    box-sizing: border-box;
}

.fontIcon {
    font-size: 1.5rem !important;
}

.card:before {
    content: '';
    position: absolute;
    width: 80%;
    height: 220%;
    background: rgba(3, 3, 3, .7);
    top: -50%;
    left: -100%;
    z-index: 1;
    transform: rotate(25deg);
    transform-origin: center top 0;
    transition: 1.1s;
}

.card:hover:before {
    left: 10%;
}

.card img {
    width: 100%;
    height: auto;
}

.card-text {
    width: 100%;
    padding: 0 20px;
    position: absolute;
    top: -100%;
    color: #fff;
    left: 0;
    z-index: 2;
    transition: 1.1s;
}


.card:hover .card-text {
    top: 40%;
}

a:hover {
    text-decoration: none !important;
}


a {
    color: #333 !important;
}

.bg-light {
    background: linear-gradient(white 10%, white) !important;
}


@media screen and (max-width: 767px ) {
    .active-link::after {
        content: '';
        display: block;
        margin: 0 auto;
        width: 5%;
        position: absolute;
        border-bottom: 2px solid teal;
        box-sizing: border-box;
    }

    .card-text .btn {
        /*width: 10%;*/
        font-size: .5em;
    }

    .card:hover .card-text {
        /*padding-bottom: 2%;*/
        top: 35%;
    }

    .card-text h3 {
        font-size: 1.2em;
    }

    .card-text h6 {
        font-size: 1em;
    }

    .card-text {
        font-size: .7em;
        width: 100%;
        position: absolute;
        top: -100%;
        color: #fff;
        left: 0;
        z-index: 2;
        transition: 1.1s;
    }

    .h3 {
        font-size: 1.6em !important;
    }

    body {
        padding-bottom: 20% !important;
    }

    .nav-item {
        font-size: .7em !important;
    }

    .navbar-toggler {
        transition: all .5s ease;
        padding: 0 !important;
        border: none !important;
    }

    .h2.project-title {
        font-size: 1.2em !important;
        margin-bottom: 1% !important;
    }

    .navbar-toggler.active {
        transform: rotate(90deg);
    }
}

@media screen and (min-width: 1200px ) {
    .pt-6 {
        padding-top: 7%;
    }
}

.nav-item {
    text-transform: uppercase;
    font-size: .9rem;
}

.h3 {
    font-weight: 600 !important;
}


